import { mapState } from 'vuex'

export default {
  props: ['head'],
  computed: {
    ...mapState({
      user: 'user'
    })
  },
  data () {
    return {
      menuState: false,
      menu: [],
      mobilStyle: null,
      account: !0,
      headerHide: false
    }
  },

  mounted () {
    this.$nextTick(() => {
      // 失焦事件[移动端]
      this.$store.state.mobile && window.addEventListener('click', e => {
        if (this.menuState && !this.$el.contains(e.target)) {
          this.toggleMneu()
        }
      })
      // 向下滚动时收起顶部[pc端]
      let scrollTop = 0
      let lastInter = null
      if (!this.$store.state.mobile) {
        window.addEventListener('scroll', () => {
          clearTimeout(lastInter)
          lastInter = setTimeout(() => {
            const scroll = window.scrollY
            this.headerHide = scrollTop < scroll
            scrollTop = scroll
          }, 100)
        })
      }
      this.updateRouter()
      this.observer.on('HeaderRouter', this.updateRouter)
    })
    this.rightShow(this.$route)
  },
  watch: {
    '$route' (to) {
      this.rightShow(to)
    }
  },
  methods: {
    /**
     * 更新菜单路由指向
     */
    updateRouter () {
      // 对权限进行判断
      this.menu = [
        {
          tag: '文章',
          sub: [
            ['首页', '/'],
            ['置顶', '#'],
            ['最新', '#'],
            ['最热', '#'],
            ['精品', '#'],
            ['讨论', '#']
          ]
        },
        {
          tag: '技术',
          sub: [
            ['JavaScript', '#'],
            ['PHP', '#'],
            ['CSS', '#'],
            ['HTML', '#']
          ]
        },
        {
          tag: '资源',
          sub: [
            ['Windows', '#'],
            ['Andorid', '#'],
            ['Web', '#'],
            ['other', '#']
          ]
        },
        {
          tag: '账号',
          sub: [
            ['登录', { name: 'user-admin-login' }, 'register'],
            ['注册', { name: 'user-admin-register' }, 'register'],
            ['发帖', { name: 'article-editArticle' }, 'admin'],
            ['安全退出', 'outLogin', 'login']
          ]
        }
      ]
      const permissions = this.$router.permissions
      this.menu.filter((item, index) => {
        if (item.sub) {
          item.sub = item.sub.filter(subItem => {
            // 设置了权限判断
            if (subItem[2]) {
              subItem = permissions(subItem[2]) ? subItem : false
            }
            return subItem
          })
        }
        return item
      })
    },

    /**
     * 开启/关闭 导航栏[移动端]
     * @param {Boolean} state 切换状态
     */
    toggleMneu (state) {
      if (typeof state === 'boolean') {
        if (this.menuState === state) return this.menuState
        this.menuState = state
      } else this.menuState = !this.menuState

      if (this.$store.state.mobile) {
        console.log(this.observer.is('body'))
        this.observer.emit('body', {
          key: 'className',
          value: this.menuState ? 'min-screen-left' : ''
        })
      }
      return this.menuState
    },

    /* 点击导航栏内部,子导航时缩小菜单[移动端事件] */
    minMenu (e) {
      let last = e.target.lastChild
      if (!last || !last.tagName || last.tagName.toLowerCase() !== 'ul') {
        this.toggleMneu()
      }
    },

    /* 运行命令 */
    runCommand (key) {
      this[key]()
    },

    /* 退出登录 */
    outLogin () {
      localStorage.removeItem('userInfo')
      this.$store.dispatch('USER', 'default')
      this.updateRouter()
      this.observer.emit('toast', {
        icon: 'zhiwen',
        text: '安全退出成功!'
      })
    },

    /* 搜索关键词 */
    searchKeyWord (e) {
      this.observer.emit('searchKeyWord', e.target.value)
      e.target.value = ''
      e.target.blur()
    },

    /* 右侧显示判断 */
    rightShow (to) {
      const path = to.path
      // 如果在登录页不显示account
      this.account = !(path === '/user/admin/login' || path === '/user/admin/register')
      // 如果登录了在首页则不显示account
      if ((path === '/' || path === '/user/center') && !isNaN(this.user.id)) {
        this.account = !1
      }
    }
  }
}
