import Music from './Music'
// 底部音乐插件
export default {
  data () {
    return {
      floatState: !1,
      floatList: !1,
      // 第一次加载
      onlyLoad: !1,
      // 选项默认色
      iconColor: '#222',
      // 默认选中
      currentTab: '',
      // 分页列表
      pages: {
        'icon-sou-suo': 'Search',
        'icon-yinle': 'Info',
        'icon-yinleliebiaoxian': 'List',
        'icon-shoucang': 'collect',
        'icon-xiazai': 'download',
        'icon-shezhi': 'Setting'
      },
      // 工具栏内容 对应执行函数名
      tools: {
      },
      // 工具栏显示个数 默认全部
      toolList: {
        'icon-sou-suo': false
      },
      // 音乐信息
      info: {
        img: '/LOGO.png',
        src: '',
        tag: '点击展开工具栏'
      }
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.Music = this.$store.state.Music = new (new Music)(this)
    })
    // 监听音乐信息
    this.observer.on('music', data => {
      this.Music.loadMusic(data.albummid, !!data.autoPlay)
    })

    window.addEventListener('resize', resize)

    let oldInnerHeight = window.innerHeight
    let resizeTime = null
    let self = this

    function resize (e) {
      // 节流
      clearTimeout(resizeTime)
      resizeTime = setTimeout(() => {
        // 防止移动端输入时键盘弹起导致布局变形 40为排除键盘
        let newInner = window.innerHeight
        if (self.$store.state.mobile && oldInnerHeight - newInner < 40) {
          let children = self.$el.children
          children[1].style.top = `${newInner - children[1].offsetHeight}px`
          children[0].style.top = `${newInner - children[0].offsetHeight - children[1].offsetHeight}px`
          children[0].style.height = `${children[0].offsetHeight}px`
        }
      }, 100)
    }
    resize()
  },
  methods: {
    /**
     * 切换音乐浮动状态
     */
    toggleFloat () {
      let self = this
      self.floatState = !self.floatState
      // 第一次点开,进行等一次加载,防止资源浪费
      if (!self.onlyLoad) {
        this.pages['icon-sou-suo'] = resolve => require(['./Search'], resolve)
        self.onlyLoad = !0
        self.currentTab = 'icon-sou-suo'
      }
      // 如果关闭时 列表为展开 则 关闭
      if (self.floatList) {
        self.floatList = !1
      }
    },

    /**
     * 切换音乐列表展示状态
     */
    toggleList () {
      this.floatList = !this.floatList
    },

    /**
     * 选项卡点击事件
     */
    selectTab (e) {
      const i = e.target.dataset.i
      if (i) {
        const page = this.pages[i]
        if (typeof page === 'string') {
          this.pages[i] = resolve => require([`./${page}`], resolve)
        }
        this.currentTab = i
      }
    },

    /**
     * 播放控制
     */
    musicConsole (e) {
      if (e.target.tagName.toLowerCase() === 'i') {
        e.stopPropagation()
        if (e.target.dataset.on) {
          this.Music[e.target.dataset.on](e.target)
        }
      }
    },

    /**
     * 进度条点击事件
     */
    progress (e) {
      e.stopPropagation()
      this.Music.jump((e.clientX / (e.toElement.offsetWidth / 100)).toFixed(2))
    },
    wheel (e) {
      e.stopPropagation();
    }
  }
}
